export const add_user = (data) =>{
    return{
      type: 'addUser',
      payload : data
    }
  }

export const remove_user = (id) =>{
    return{
      type: 'removeUser',
      payload : id
    }
}

export const update_user = (id) =>{
  return{
    type: 'updateUser',
    payload : id
  }
}

