const initialState = {
  users : [
    {
      id : 111,
      name: "Praveen",
      age : 24,
      place : "Karimnagar"
    },
    {
      id : 222,
      name: "Dan Abramov",
      age: 55,
      place : "America"
    }
  ]
}
function rootReducer(state = initialState.users, action){
  const get_users = [...state];
    switch(action.type){
      case 'addUser':
        const new_user = action.payload;
        const updated_users = [
          ...state,  new_user
        ]
        return updated_users;
      case 'removeUser':
        const filtered_users = get_users.filter( user => user.id !== action.payload );
        return filtered_users;
      case 'updateUser':
        const get_user = action.payload;
        const modified_users = get_users.map( user => {
          if(user.id === get_user.id){
            user.name = get_user.name;
            user.age = get_user.age;
            user.place = get_user.place;
          }
          return user;
        });
        return modified_users;
      default:
        return state;
    }
  }

  export default rootReducer;