import React, {useState} from 'react';
import { remove_user } from "../actions/index";
import {useSelector} from 'react-redux';
import { useDispatch } from "react-redux";
import EditUser from './EditUser';

function Users() {
  const [edit_user_id, setEditUser] = useState(0);
  const users = useSelector(state => state);
  const dispatch = useDispatch();
  const removeUser = (id) =>{
    //alert("remove alert");
    dispatch(remove_user(id));
  }
  const editUser = (id) =>{
    setEditUser(id);
    document.getElementById('edit-popup').style.display = "block";
  }
  const users_element = users.map((user) =>
      <tr key={user.id}>
        <td>{user.name}</td>
        <td>{user.age}</td>
        <td>{user.place}</td>
        <td className="btns-user">
          <button className="btn btn-edit btn-primary"  onClick={()=>editUser(user.id)}>Edit</button>
          <button className="btn btn-remove btn-danger" onClick={()=>removeUser(user.id)}>Remove</button>
        </td>
      </tr>
    );
  return (
    <>
    <section>
    <div className="section-title">Users List</div>
    <table className="users">
      <tbody>
        <tr className="table-head">
          <td>Name</td>
          <td>Age</td>
          <td>Location</td>
          <td>settings</td>
        </tr>
        {users_element}
      </tbody>
    </table>
    </section>
    <EditUser user_id = {edit_user_id}/>
    </>
  );
}

export default Users;
