import React from 'react';
import { useForm } from "react-hook-form";
import { update_user } from "../actions/index";
import {useSelector} from 'react-redux';
import { useDispatch } from "react-redux";

function EditUser ({user_id}){
    const { handleSubmit, register } = useForm();
    const users = useSelector(state => state);
    const dispatch = useDispatch();
    const closePopup = (id) =>{
        document.getElementById('edit-popup').style.display = "none";
    }
    users.forEach(user => {
        if(user.id === user_id){
            document.getElementById('edit_name').value = user.name;
            document.getElementById('edit_age').value = user.age;
            document.getElementById('edit_place').value = user.place;
        }
    });
    const onSubmit = (data) => {
        data.id =  user_id;
        dispatch(update_user(data));
        document.getElementById('edit-popup').style.display = "none";
    };
    return(
        <div id="edit-popup">
            <div className="card">
                <span onClick={()=>closePopup()}className="close-edit text-primary">&#215;</span>
                <div className="card-body">
                <form onSubmit={handleSubmit(onSubmit)}>
                    <div className="form-row">
                    <div className="col">
                        <input
                        ref={register}
                        type="text"
                        name="name"
                        className="form-control edit-name"
                        id="edit_name"
                        placeholder="anonymous"
                        />
                    </div>
                    <div className="col">
                        <input
                        ref={register}
                        type="number"
                        name="age"
                        className="form-control edit-age"
                        id="edit_age"
                        placeholder="99"
                        />
                    </div>
                    <div className="col">
                        <input
                        ref={register}
                        type="text"
                        name="place"
                        className="form-control edit-place"
                        id="edit_place"
                        placeholder="somewhere else"
                        />
                    </div>
                    <div className="col">
                        <button className="btn btn-success btn-add" type="submit">
                        Update
                        </button>
                    </div>
                    </div>
                </form>
                </div>
            </div>
        </div>
    )
}

export default EditUser;
