import React from "react";
import { useForm } from "react-hook-form";
import { useDispatch } from "react-redux";
import { add_user } from "../actions/index";

const AddUser = () => {
  const { handleSubmit, register } = useForm();
  const dispatch = useDispatch();
  const onSubmit = (data) => {
    data.id =  Math.floor(Math.random() * 999);
    dispatch(add_user(data));
  };

  return (
    <section>
      <div className="section-title">Add New User</div>
      <form onSubmit={handleSubmit(onSubmit)}>
        <div className="form-row">
          <div className="col">
            <input
              ref={register}
              type="text"
              name="name"
              required
              className="form-control"
              id="formGroupExampleInput"
              placeholder="anonymous"
            />
          </div>
          <div className="col">
            <input
              ref={register}
              type="number"
              name="age"
              className="form-control"
              id="formGroupExampleInput2"
              placeholder="99"
            />
          </div>
          <div className="col">
            <input
              ref={register}
              type="text"
              name="place"
              className="form-control"
              id="formGroupExampleInput2"
              placeholder="somewhere else"
            />
          </div>
          <div className="col">
            <button className="btn btn-success btn-add" type="submit">
              Add New User
            </button>
          </div>
        </div>
      </form>
    </section>
  );
};

export default AddUser;
